import $ from 'jquery'
import layui from 'layui'
import Base from 'src/scripts/common/base'
import '../../styles/setting.scss'
import modBase from "src/scripts/common/moduleBase"

let layer, laytpl, laydate, element, upload, form, adminMsg
export default Object.assign({
    api: {
        EditAdmin: {
            type: 'POST',
            url: '/api/Admin/EditAdmin'
        },
        ChangePwd: {
            type: 'POST',
            url: '/api/Admin/ChangePwd'
        },
        EditArea: {
            type: 'POST',
            url: '/api/Admin/EditArea'
        },
        EditSchoolUser: {
            type: 'POST',
            url: '/api/Admin/EditSchoolUser'
        }
    },
    // 
    init (data) {
        let me = this
        // adminMsg = modBase.$get('AdminMsg')
        adminMsg = modBase.$getS('AdminMsg')
        if (adminMsg) {
            layui.use(['laytpl', 'layer', 'laydate', 'element', 'form', 'upload'], function () {
                layer = layui.layer, laytpl = layui.laytpl, laydate = layui.laydate, element = layui.element, form = layui.form, upload = layui.upload
                me.renderPage()
                me.bindEvt()
            })
        }else {
            me.$route.push('setting')
        }
    },

    // 
    renderPage () {
        let me = this, getTpl = $('#settingTpl').html(), $view = $('#setting')
        laytpl(getTpl).render(adminMsg, function(dom){
            $view.html(dom)
        })
    },

    // 
    bindEvt () {
        let me = this
        // 
        $('#setting').on('click', '.setAdminMsg', function () {
            let data, _api
            if (adminMsg.Type === 0) {
                data = {
                    Account: $('.setting input[name="Account"]')[0].value,
                    Name: $('.setting input[name="Name"]')[0].value
                }
                _api = me.api['EditAdmin']
            } else if (adminMsg.Type === 1) {
                data = {
                    ID: $('.setting input[name="Account"]')[0].value,
                    PersonName: $('.setting input[name="Name"]')[0].value,
                    Tel: $('.setting input[name="Tel"]')[0].value,
                    Email: $('.setting input[name="Email"]')[0].value,
                    QQ: $('.setting input[name="QQ"]')[0].value
                }
                _api = me.api['EditSchoolUser']
            } else if (adminMsg.Type === 2 ) {
                data = {
                    ID: $('.setting input[name="Account"]')[0].value,
                    PersonName: $('.setting input[name="Name"]')[0].value,
                    Tel: $('.setting input[name="Tel"]')[0].value,
                    Email: $('.setting input[name="Email"]')[0].value,
                    QQ: $('.setting input[name="QQ"]')[0].value
                }
                _api = me.api['EditArea']
            }
            me.editAdmin(_api, data)
        })
        // 
        $('#setting').on('click', '#changePsw', function () {
            me.changePwd()
        })
    },

    // 
    editAdmin (api, data) {
        let me = this
        if (data.Name === '') {
            layer.msg('')
        } else if (data.PersonName === '') {
            if (adminMsg.Type === 1) {
                layer.msg('')
            } else {
                layer.msg('')
            }
        } else if (data.Tel === '') {
            layer.msg('')
        } else if (data.Email === '') {
            layer.msg('')
        } else if (data.QQ === '') {
            layer.msg('QQ')
        } else {
            modBase.$layMsg('', function () {
                modBase.$api(api, data)
                    .then(res => {
                        layer.msg('')
                        let Name = data.Name
                        adminMsg.PersonName = Name
                        $('#userInfo').html(Name)
                        modBase.$setS('AdminMsg', adminMsg)
                        me.renderPage()
                    })
                    .catch(err => {me.$handleErr(err)})
            })
        }
    },

    // 
    changePwd () {
        let me = this, getTpl = $('#changePswTpl').html(), dialog
        dialog = layer.open({
            title: '',
            // area: '400px',
            type: 1,
            btn: ['', ''],
            content: getTpl,
            btnAlign: 'c',
            success: function () {$(':focus').blur()},
            yes: function () {
                let OldPwd, NewPwd1, NewPwd2
                OldPwd = $('.changePswCont input[name="OldPwd"]')[0].value
                NewPwd1 = $('.changePswCont input[name="NewPwd1"]')[0].value
                NewPwd2 = $('.changePswCont input[name="NewPwd2"]')[0].value
                if (OldPwd === '') {
                    layer.msg('')
                } else if (NewPwd1 === '') {
                    layer.msg('')
                } else if (NewPwd2 === '') {
                    layer.msg('')
                } else if (NewPwd1 !== NewPwd2) {
                    layer.msg('')
                } else {
                    modBase.$layMsg('', function () {
                        modBase.$api(me.api['ChangePwd'], {
                            Account: adminMsg.ID,
                            OldPwd: OldPwd,
                            NewPwd: NewPwd1
                        })
                            .then(res => {
                                layer.msg('')
                                layer.close(dialog)
                                modBase.$clearS()   // session
                                me.$route.push('login') // route
                            })
                            .catch(err => {me.$handleErr(err)})
                    })
                }
            }
        })
    }
}, Base)


